.action-bar {
  border: 1px solid var(--gray-40);
  border-left: 0; // Don't double the border with the filters
  background-color: var(--green-20);
  padding: @gutter-normal @grid_gutter-width;

  h2 {
    margin: 0;

    @media @phone {
      padding: @gutter-normal;
    }
  }

  .export-results {
    padding-top: @gutter-minimum;
    margin: 0;
    margin-bottom: @gutter-minimum;
    .a-btn {
      margin-right: @gutter-normal;
      font-size: @size-v;

      svg {
        margin-right: @gutter-minimum;
      }
    }
  }

  .a-btn--link {
    color: var(--pacific-dark);

    &:hover {
      color: var(--navy);
    }

    &:focus {
      color: var(--pacific-dark);
      border: 1px dotted var(--pacific-dark);
    }

    &:active {
      color: var(--navy);
      border: 1px dotted var(--navy);
    }
  }

  @media @phone {
    padding-left: 0;
    border: 1px solid var(--gray-40);
    .export-results {
      margin-left: 15px;
    }
  }

  @media @superwide {
    padding-left: 40px;
  }
}

